import React from 'react';

import useThemes from '../utils/hooks/useThemes';
import AnimatedBorders from '../components/AnimatedBorders';
import NavigationBar from '../components/NavigationBar';
import HeroSection from '../components/HeroSection';

const Home = () => { 
    const [theme, isMounted, toggleThemeState] = useThemes();  
    const processedTheme = theme === 'dark' ? 'dark' : 'light';

    return (
        <>
            <AnimatedBorders />
            <div className="fixed transition ease-linear duration-300 top-0 w-full h-full bg-transparent dark:bg-gray-800 -z-1" />
            <div className="m-8 md:m-14 lg:m-20">
                <NavigationBar 
                    isMounted={isMounted}
                    toggleThemeState={toggleThemeState}
                />
                {isMounted && <HeroSection />}
            </div>
        </> 
    );
};

export default Home;