import React from 'react';

import SunSvg from '../../svgs/sun.svg';
import MoonSvg from '../../svgs/moon.svg';

const NavigationBar = ({ isMounted, toggleThemeState }) => {
    return (
        <div className="flex items-center">
            <div className="flex w-1/2 lg:w-1/3">
                Test
            </div>
            <div className="hidden w-1/3 lg:flex">

            </div>
            <div className="flex w-1/2 lg:w-1/3 justify-end items-center">
                <SunSvg alt="light" id="sun-light" className="mr-2 w-7 h-7" />
                <div
                    id="dark-mode-slider"
                    onClick={toggleThemeState}
                    className="relative flex items-center rounded-2xl bg-blue-light dark:bg-fuchsia-light w-16 h-8 cursor-pointer"
                >
                    <div 
                        id="dark-mode-circle"
                        className="absolute ml-0.5 w-7 h-7 bg-white rounded-full" 
                    />
                </div>
                <MoonSvg alt="dark" id="moon-dark" className="ml-2 w-6 h-6" />
            </div>
        </div>
    )
};

export default NavigationBar;